.header {
    background-color: $violet-1-1;
}

.header-top {

    padding: 16px 0;
    display: flex;

    flex-wrap: wrap;

    @media screen and (min-width: $w992) {
        flex-wrap: nowrap;
    }

    &__main {
        display: flex;
        align-items: center;
    }

    &__actions {

        display: flex;
        align-items: center;
        margin-left: auto;

        @media screen and (min-width: $w992) {
            margin-left: unset
        }



        &>div {
            @media screen and (min-width: $w760) {
                margin-left: 12px;
            }
        }
    }

    &__find {
        width: 100%;
        order: 3;
        padding-top: 12px;

        @media screen and (min-width: $w992) {
            order: unset;
            padding-top: 0;
        }
    }

    &__logo {

        margin-right: 16px;

        @media screen and (min-width: $w1200) {
            margin-right: 47px;
        }

        & .bgimage {
            width: 96px;
            height: 36px;
            @include bgimage-contain;

            @media screen and (min-width: $w760) {
                width: 108px;
                height: 40px;
            }
        }
    }

    &__login,
    &__compare,
    &__fav {
        margin-left: 8px;

        @media screen and (min-width: $w992) {
            margin-left: 0;
        }
    }

    & .dropdown-icon,
    & .tooltip-icon {
        &__elem {
            & a {
                background-color: $violet-1-2;
                border-radius: 6px;

                & svg {
                    filter: grayscale(1) brightness(3);
                    transition: 0.3s ease;
                }

                &:hover {

                    background-color: $violet-1-2;

                    & svg {
                        filter: none;
                    }
                }
            }
        }
    }

    & .search-index {
        &__input {
            & input {
                background-color: $violet-1-2;
                color: #fff;
            }
        }

        & .btn.btn-catalog {
            @media screen and (max-width: $w1200) {
                width: 48px;
                min-width: 48px;
                font-size: 0;

                & .icon-catalog {
                    margin-right: 0;
                }
            }
        }
    }

}

.header-bottom {

    color: #fff;
    display: flex;
    align-items: center;

    padding: 0 0 16px 0;

    &__menu {

        flex-grow: 1;
        display: none;

        @media screen and (min-width: $w1200) {
            display: block;
        }

        & nav {
            width: 100%;

            @media screen and (min-width: $w1440) {
                padding-right: 160px;
                white-space: nowrap;
                overflow: hidden;

            }

            @media screen and (min-width: $w1200) {
                white-space: nowrap;
                padding-right: 0;
            }
        }

        & ul {
            width: 100%;
            display: flex;

            & a {
                color: $violet-2-1;
                transition: 0.3s ease;
            }

            & li {
                flex-grow: 1;
                padding: 0 10px;

                &:hover {
                    & a {
                        color: #fff;
                    }
                }
            }
        }
    }

    &__location {

        @media screen and (min-width: $w1440) {
            margin-right: 80px;
        }

        @media screen and (min-width: $w1200) {
            margin-right: 30px;
        }



        & .header-location {
            & svg {
                filter: grayscale(1) brightness(3);
                width: 18px;
            }
        }
    }




}